import { ITagsQuery } from './tags.types'
import { MockedResponse } from '@apollo/client/testing'
import { GET_TAGS } from './tags.query'
import { TagType } from './tags.types'
import { sourceTags, contentTags, sentimentTags } from './tags.db'

export const tagsQueryMock: Array<MockedResponse<ITagsQuery>> = [
  {
    request: {
      query: GET_TAGS,
      variables: {
        type: TagType.Content,
      },
    },
    result: {
      data: {
        tags: contentTags,
      },
    },
  },
  {
    request: {
      query: GET_TAGS,
      variables: {
        type: TagType.Source,
      },
    },
    result: {
      data: {
        tags: sourceTags,
      },
    },
  },
  {
    request: {
      query: GET_TAGS,
      variables: {
        type: TagType.Sentiment,
      },
    },
    result: {
      data: {
        tags: sentimentTags,
      },
    },
  },
]
