import React, { useEffect, useState } from 'react'
import { tokenPath } from '../constants/Config'

export interface ITokenContext {
  token: string | null
  setToken: (token: string) => void
}

export const TokenContext = React.createContext<ITokenContext>({
  token: null,
  setToken: () => {},
})

export interface ITokenProviderProps {
  children?: JSX.Element
}

export const TokenProvider = (props: ITokenProviderProps) => {
  const [token, setToken] = useState(() => {
    return localStorage.getItem(tokenPath)
  })
  useEffect(() => {
    if (typeof token === 'string') localStorage.setItem(tokenPath, token)
  }, [token])

  return (
    <TokenContext.Provider value={{ token, setToken }}>
      {props.children}
    </TokenContext.Provider>
  )
}

export const TokenConsumer = TokenContext.Consumer
