import gql from 'graphql-tag'

export const GET_ARTICLES = gql`
  query ArticleQuery(
    $offset: Int
    $limit: Int
    $contentTag: String
    $sourceTag: String
  ) {
    articles(
      offset: $offset
      limit: $limit
      filters: { contentTag: $contentTag, sourceTag: $sourceTag }
    ) {
      list {
        articleId
        title
        thumbnail
        breakingNews
        sendSMS
        sourceTag {
          title
          value
        }
        sentimentTag {
          title
          value
        }
        contentTag {
          title
          value
        }
        date
        url
        isVideo
      }

      info {
        total
      }
    }
  }
`
