import * as React from 'react'
import { SafeAreaView, StyleSheet, TextInput, View } from 'react-native'

import { ArticleTabParamProps, RootTabScreenProps } from '../types'
import { FiltersConsumer } from '../context'
import { ListComponent } from '../components/ListComponent'
import Colors from '../constants/Colors'

const styles = StyleSheet.create({
  safeAreaView: {
    flex: 1,
  },
  sectionStyle: {
    flexDirection: 'row',
    height: 40,
    margin: 10,
  },
  inputStyle: {
    flex: 1,
    color: 'gray',
    paddingLeft: 15,
    paddingRight: 15,
    borderWidth: 1,
    borderRadius: 30,
    borderColor: '#dadae8',
    outlineColor: Colors.orangeWeb,
  },
})
export const SearchScreen: React.FC<ArticleTabParamProps<'ArticleList'>> = ({
  navigation,
}) => {
  const [searchString, setSearchString] = React.useState('')
  const [layout, setLayout] = React.useState({
    width: 0,
    height: 0,
  })
  return (
    <SafeAreaView
      style={styles.safeAreaView}
      onLayout={(event) => setLayout(event.nativeEvent.layout)}
    >
      <View style={styles.sectionStyle}>
        <TextInput
          style={styles.inputStyle}
          onChangeText={(searchString) => setSearchString(searchString)}
          placeholder="Keress"
          placeholderTextColor="#8b9cb5"
          autoCapitalize="none"
          keyboardType="email-address"
          returnKeyType="next"
          underlineColorAndroid="#f000"
          blurOnSubmit={false}
        />
      </View>
      <FiltersConsumer>
        {({ sourceTag, contentTag }) => {
          return (
            <ListComponent
              layout={layout}
              navigation={navigation}
              sourceTag={sourceTag}
              contentTag={contentTag}
            />
          )
        }}
      </FiltersConsumer>
    </SafeAreaView>
  )
}
