// Example of Splash, Login and Sign Up in React Native
// https://aboutreact.com/react-native-login-and-signup/
import 'react-native-gesture-handler'

// Import React and Component
import React, { FunctionComponent, useContext } from 'react'

// Import Navigators from React Navigation
import { DefaultTheme, NavigationContainer } from '@react-navigation/native'
import { createNativeStackNavigator } from '@react-navigation/native-stack'
import { StyleSheet } from 'react-native'

import LinkingConfiguration from './LinkingConfiguration'
import Colors from '../constants/Colors'
import LoginScreen from '../screens/LoginScreen'
import { TokenContext } from '../context'
import { RootNavigator } from './RootNavigator'
import SettingsScreen from '../screens/SettingsScreen'
import HelpScreen from '../screens/HelpScreen'
import DetailsScreen from '../screens/DetailsScreen'
import NotFoundScreen from '../screens/NotFoundScreen'
import CategoriesScreen from '../screens/CategoriesScreen'

const Stack = createNativeStackNavigator()

export interface IAuthNavigationProps {}

const styles = StyleSheet.create({
  screenOptionsHeader: {
    height: 50,
    backgroundColor: Colors.oxfordBlue,
    justifyContent: 'space-between',
    alignItems: 'center',
    flexDirection: 'row',
  },
})

export const AuthNavigation: FunctionComponent<IAuthNavigationProps> = ({}) => {
  const { token } = useContext(TokenContext)
  return (
    <NavigationContainer linking={LinkingConfiguration} theme={DefaultTheme}>
      <Stack.Navigator
        screenOptions={{
          headerBackButtonMenuEnabled: true,
          headerStyle: styles.screenOptionsHeader,
          headerTintColor: Colors.orangeWeb,
        }}
      >
        {!token ? (
          <Stack.Screen
            name="Login"
            component={LoginScreen}
            options={{ headerShown: false }}
          />
        ) : (
          <>
            <Stack.Screen
              name="Home"
              component={RootNavigator}
              options={{ headerShown: false }}
            />
            <Stack.Screen
              name="Settings"
              component={SettingsScreen}
              options={{ headerTitle: 'Belltsok' }}
            />
            <Stack.Screen
              name="Categories"
              component={CategoriesScreen}
              options={{ headerTitle: 'Kategrik' }}
            />
            <Stack.Screen
              name="Help"
              component={HelpScreen}
              options={{ headerTitle: 'Segtsg' }}
            />
            <Stack.Screen
              name="Details"
              component={DetailsScreen}
              options={{ headerTitle: 'Rszletek' }}
            />
            <Stack.Screen
              name="NotFound"
              options={{ headerShown: false }}
              component={NotFoundScreen}
            />
          </>
        )}
      </Stack.Navigator>
    </NavigationContainer>
  )
}
