import { makeExecutableSchema } from '@graphql-tools/schema'

import gql from 'graphql-tag'

const typeDefs = gql`
  type ArticlesInfo {
    total: Int!
  }
  type ArticlesResult {
    list: [Article!]!
    info: ArticlesInfo
  }

  type ArticleQuery {
    articles: ArticlesResult!
  }

  type Tag {
    title: String!
    value: String!
  }

  type Article {
    articleId: ID!
    title: String!
    thumbnail: String!
    breakingNews: Boolean!
    sendSMS: Boolean!
    sourceTag: Tag!
    sentimentTag: Tag!
    contentTag: [Tag!]!
    date: String!
    url: String!
    isVideo: Boolean!
  }
`

export const ArticlesSchema = makeExecutableSchema({ typeDefs })
