import { IArticleQuery } from './articles.types'
import { MockedResponse } from '@apollo/client/testing'
import { GET_ARTICLES } from './articles.query'
import { ITag } from '../tags'
import { sourceTags, contentTags, allTag } from '../tags/tags.db'
import { omitEmpty } from '../../utils'
import { ARTICLE_NUMBER, ARTICLE_PER_PAGE, articles } from './articles.db'

export const articlesQueryMock: Array<MockedResponse<IArticleQuery>> = [
  ...[allTag, ...sourceTags].reduce(
    (memo: Array<MockedResponse<IArticleQuery>>, sourceTag: ITag) => {
      return [
        ...memo,
        ...[allTag, ...contentTags].reduce(
          (memo: Array<MockedResponse<IArticleQuery>>, contentTag: ITag) => {
            return [
              ...memo,
              ...[
                ...Array(Math.ceil(ARTICLE_NUMBER / ARTICLE_PER_PAGE)).keys(),
              ].map((i) => {
                const contentFiltered = articles.filter((a) =>
                  a.contentTag
                    .map((t) => t.value)
                    .join(' ')
                    .includes(contentTag.value || '')
                )

                const sourceFiltered = contentFiltered.filter((a) =>
                  a.sourceTag.value.includes(sourceTag.value || '')
                )

                const data = {
                  __typename: 'ArticleQuery' as 'ArticleQuery',
                  articles: {
                    info: { total: sourceFiltered.length },
                    list: [...sourceFiltered].splice(
                      i * ARTICLE_PER_PAGE,
                      ARTICLE_PER_PAGE
                    ),
                  },
                }
                return {
                  request: {
                    query: GET_ARTICLES,
                    variables: omitEmpty({
                      offset: i * ARTICLE_PER_PAGE,
                      limit: ARTICLE_PER_PAGE,
                      contentTag: contentTag.value,
                      sourceTag: sourceTag.value,
                    }),
                  },
                  result: {
                    data,
                  },
                  newData: () => ({
                    data,
                  }),
                }
              }),
            ]
          },
          []
        ),
      ]
    },
    []
  ),
]
