import { StatusBar } from 'expo-status-bar'
import React from 'react'
import { SafeAreaProvider } from 'react-native-safe-area-context'
import AppLoading from 'expo-app-loading'
import {
  useFonts,
  Roboto_300Light,
  Roboto_400Regular,
  Roboto_500Medium,
} from '@expo-google-fonts/roboto'

import useCachedResources from './hooks/useCachedResources'
import Navigation from './navigation'
import { register } from './workers/serviceWorkerRegistration'
import { InstallPrompt } from './components/InstallPrompt'
import { getInstalledStatus } from './constants/Config'
import InstallScreen from './screens/InstallScreen'
import { InstallStatusProvider, TokenProvider } from './context'
import { ClientProvider } from './api'

register()

export default function App() {
  const isLoadingComplete = useCachedResources()
  const isInstalled = getInstalledStatus()
  let [fontsLoaded] = useFonts({
    Roboto_300Light,
    Roboto_400Regular,
    Roboto_500Medium,
  })

  if (!isLoadingComplete || !fontsLoaded) {
    return <AppLoading />
  } else {
    return (
      <ClientProvider useMocks>
        <InstallStatusProvider>
          <TokenProvider>
            <SafeAreaProvider>
              <InstallPrompt />
              {isInstalled ? <Navigation /> : <InstallScreen />}
              <StatusBar />
            </SafeAreaProvider>
          </TokenProvider>
        </InstallStatusProvider>
      </ClientProvider>
    )
  }
}
