import React, { FunctionComponent } from 'react'
import { createMaterialTopTabNavigator } from '@react-navigation/material-top-tabs'
import Colors from '../constants/Colors'
import { ArticleTabParamList, ArticleTabParamProps } from '../types'
import { FiltersProvider, UserConsumer } from '../context'
import { TopTabNavigationBar } from '../components/TopTabNavigationBar'

const TopTab = createMaterialTopTabNavigator<ArticleTabParamList>()

export const TopTabNavigator: FunctionComponent<
  ArticleTabParamProps<'ArticleList'>
> = ({ route, children }) => {
  const { sourceTag } = route.params
  return (
    <FiltersProvider sourceTag={sourceTag}>
      <UserConsumer>
        {(user) => (
          <TopTab.Navigator
            tabBar={(
              props: JSX.IntrinsicAttributes & {
                state: any
                descriptors: any
                navigation: any
                position: any
              }
            ) => <TopTabNavigationBar {...props} />}
            screenOptions={{
              tabBarActiveTintColor: Colors.oxfordBlue,
              tabBarScrollEnabled: true,
              tabBarStyle: { height: 50 },
            }}
          >
            <TopTab.Screen
              name="ArticleList"
              initialParams={{ sourceTag } as any}
              options={{
                title: 'Minden Tma',
              }}
            >
              {() => (
                <FiltersProvider sourceTag={sourceTag}>
                  {children as any}
                </FiltersProvider>
              )}
            </TopTab.Screen>
            {user?.preferredTags.map((tag) => (
              <TopTab.Screen
                key={tag.value}
                name={`${tag.value}List` as any}
                options={{
                  title: tag.title,
                }}
              >
                {() => (
                  <FiltersProvider sourceTag={sourceTag} contentTag={tag.value}>
                    {children as any}
                  </FiltersProvider>
                )}
              </TopTab.Screen>
            ))}
          </TopTab.Navigator>
        )}
      </UserConsumer>
    </FiltersProvider>
  )
}
