import React from 'react'
import { View, Text, StyleSheet } from 'react-native'
import Icon from 'react-native-vector-icons/MaterialIcons'
import Colors from '../constants/Colors'
import { UserConsumer, UserProvider } from '../context'
import JSON from '../package.json'
import { NavbarIconButton } from '../components/NavbarIconButton'

const styles = StyleSheet.create({
  containerView: {
    backgroundColor: Colors.white,
    height: window.screen.height - 50,
  },
  headerView: {
    height: 40,
    backgroundColor: Colors.platinum,
    alignItems: 'flex-start',
    justifyContent: 'center',
  },
  headerText: {
    color: Colors.oxfordBlue,
    fontFamily: 'Roboto_500Medium',
    fontSize: 17,
    marginHorizontal: 13,
  },
  view: {
    height: 50,
    alignItems: 'center',
    justifyContent: 'space-between',
    flexDirection: 'row',
  },
  viewSplit: {
    borderBottomWidth: 1,
    borderBottomColor: Colors.platinum,
  },
  text: {
    color: Colors.oxfordBlue,
    fontFamily: 'Roboto_400Regular',
    fontSize: 15,
    marginHorizontal: 13,
  },
  icon: {
    marginHorizontal: 13,
  },
  info: {
    marginHorizontal: 13,
    fontSize: 14,
    fontFamily: 'Roboto_300Light',
    color: Colors.oxfordBlue,
  },
})

export default function SettingsScreen(props: any) {
  const { navigation } = props
  return (
    <UserProvider>
      <UserConsumer>
        {(user) => {
          return (
            <View style={styles.containerView}>
              <View style={styles.headerView}>
                <Text style={styles.headerText}>Hrek</Text>
              </View>
              <View style={styles.view}>
                <Text style={styles.text}>Kategrik</Text>
                <NavbarIconButton
                  navigation={navigation}
                  icons={[
                    {
                      name: 'keyboard-arrow-right',
                      direction: 'Categories',
                    },
                  ]}
                />
              </View>
              <View style={styles.headerView}>
                <Text style={styles.headerText}>Rlunk</Text>
              </View>
              <View style={[styles.view, styles.viewSplit]}>
                <Text style={styles.text}>Verzi {JSON.version}</Text>
              </View>
              <View style={styles.view}>
                <Text style={styles.text}>E-mail</Text>
                <Text style={styles.info}>{user?.email}</Text>
              </View>
            </View>
          )
        }}
      </UserConsumer>
    </UserProvider>
  )
}
