import React, { useState } from 'react'
import { View, Text, StyleSheet } from 'react-native'
import Colors from '../constants/Colors'
import {
  ContentTagsConsumer,
  ContentTagsProvider,
  UserConsumer,
  UserProvider,
} from '../context'
import { Switch } from 'react-native-switch'

const styles = StyleSheet.create({
  containerView: {
    backgroundColor: Colors.white,
    height: window.screen.height - 50,
  },
  view: {
    height: 50,
    alignItems: 'center',
    justifyContent: 'space-between',
    flexDirection: 'row',
  },
  text: {
    color: Colors.oxfordBlue,
    fontFamily: 'Roboto_400Regular',
    fontSize: 15,
    marginHorizontal: 13,
  },
  switch: {
    marginHorizontal: 13,
  },
})

export default function CategoriesScreen() {
  const toggleSwitch = () => {}
  return (
    <UserProvider>
      <ContentTagsProvider>
        <UserConsumer>
          {(user) => {
            return (
              <ContentTagsConsumer>
                {(content) => {
                  return (
                    <View style={styles.containerView}>
                      {content?.map((contentItem) => {
                        return (
                          <View style={styles.view} key={contentItem.value}>
                            <Text style={styles.text}>{contentItem.title}</Text>
                            <Switch
                              onValueChange={toggleSwitch}
                              value={
                                user?.preferredTags.find(
                                  (tag) => tag.value === contentItem.value
                                )
                                  ? true
                                  : false
                              }
                              backgroundActive={Colors.orangeWeb + 30}
                              backgroundInactive={Colors.oxfordBlue + 30}
                              activeText=""
                              inActiveText=""
                              containerStyle={{ marginHorizontal: 13 }}
                              circleSize={16}
                              barHeight={10}
                              circleActiveColor={Colors.orangeWeb}
                              circleInActiveColor={Colors.oxfordBlue}
                              circleBorderWidth={0}
                              switchWidthMultiplier={1.5}
                              switchLeftPx={8}
                              switchRightPx={8}
                            />
                          </View>
                        )
                      })}
                    </View>
                  )
                }}
              </ContentTagsConsumer>
            )
          }}
        </UserConsumer>
      </ContentTagsProvider>
    </UserProvider>
  )
}
