import * as React from 'react'
import {
  FlatList,
  SafeAreaView,
  Pressable,
  StyleSheet,
  Text,
} from 'react-native'
import AppLoading from 'expo-app-loading'
import { RefreshControl } from '../components/RefreshControl'

import { Tile } from '../components/Tile'

import { useQuery } from '@apollo/client'
import { IArticleQuery } from '../api/articles/articles.types'
import { GET_ARTICLES } from '../api/articles/articles.query'
import { client } from '../api'
import { omitEmpty } from '../utils'

const styles = StyleSheet.create({
  flatList: {
    backgroundColor: '#FBFBF8',
    alignItems: 'center',
    justifyContent: 'center',
  },
  empty: { fontWeight: 'bold', marginVertical: 20, color: 'gray' },
})

export interface IListComponentProps {
  navigation: any
  layout: { width: number; height: number }
  contentTag?: string | null
  sourceTag?: string | null
}

export const ListComponent: React.FC<IListComponentProps> = ({
  navigation,
  contentTag,
  sourceTag,
  layout,
}) => {
  const variables = omitEmpty({
    offset: 0,
    limit: 10,
    contentTag,
    sourceTag,
  })
  const { data, fetchMore, loading } = useQuery<IArticleQuery>(GET_ARTICLES, {
    variables,
    notifyOnNetworkStatusChange: true,
  })
  const list = data?.articles.list || []
  const total = data?.articles.info.total || 0
  return (
    <FlatList
      contentContainerStyle={styles.flatList}
      data={list}
      keyExtractor={(article) => `${article.articleId}`}
      onEndReached={() => {
        if (!data || total <= list.length) return

        fetchMore({
          variables: {
            ...variables,
            offset: list.length,
          },
        })
      }}
      onEndReachedThreshold={0.5}
      refreshControl={
        <RefreshControl
          refreshing={loading}
          onRefresh={() => {
            client.resetStore()
          }}
        />
      }
      renderItem={({ item }) =>
        layout.width > 0 ? (
          <Pressable
            onPress={() => {
              navigation.navigate('Details', {
                externalPath: item.url,
              })
            }}
            style={({ pressed }) => ({
              opacity: pressed ? 0.5 : 1,
              flex: 1,
              width: layout.width,
            })}
          >
            <Tile article={item} />
          </Pressable>
        ) : (
          <AppLoading />
        )
      }
      ListEmptyComponent={
        <Text style={styles.empty}>
          Nincs a keressi feltteleknek megfelel cikk
        </Text>
      }
    />
  )
}
